@import '../../../../assets/styles/sass/abstracts/variables.scss';

.section {
    min-height: 100vh;
    width: 100%;
    position: relative;
    overflow: hidden;

    &BgBox {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
        // pointer-events: none;
    }

    &BgImg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &Container {
        padding-top: 10rem;
    }

    .globalContainer {
        @extend %globalContainer;
    }

    &Content {
        // color: $light;
        display: flex;
        justify-content: space-between;
    }
}

.experience {
    width: 42%;
    padding: 4.375rem 4.6875rem 3.125rem 3.125rem;
    text-align: right;
    border: 1px solid rgba(255, 255, 255, .4);
    border-right: none;
    margin-right: 6rem;

    &Years {
        font-family: Poppins, sans-serif;
        font-size: 12.5rem;
        font-weight: 500;
        line-height: 1.1;
        margin-right: .75rem;
        margin-bottom: 5rem;

        &Square {
            display: inline-block;
            background-color: $active;
            width: 1rem;
            height: 1rem;
            margin-left: -1.5rem;
        }
    }

    &Text {
        font-size: 1.4rem;
        font-weight: 400;
        line-height: 1.5;
        position: relative;

        &::before {
            position: absolute;
            content: '';
            width: 8rem;
            height: 1px;
            left: 1.5rem;
            bottom: 0;
            background-color: $light;
        }
    }
}

.bestBlock {
    width: 47%;

    &Title {
        @extend %title;
        margin-bottom: 3rem;
    }

    &Text {
        @extend %textSection;
        max-width: 25rem;
        margin-bottom: 3rem;
    }

    &Item {
        margin-bottom: 2.5rem;
    }

    &ItemContent {
        display: flex;
        justify-content: space-between;
        padding-bottom: .5rem;
    }

    &ItemText {
        text-transform: uppercase;
    }

    &ItemContentBorder {
        width: 100%;
        height: 0.5rem;
        background-color: $light;
        border-radius: .45rem;
    }

    &ItemLine {
        height: .5rem;
        background-color: $active;
        margin-top: -.5rem;
        border-radius: .45rem;
    }

    &ItemLineAnimation {
        animation: countOne 2s linear;
    }

}

// !animation

@keyframes countOne {
    0% {
        width: 0;
    }

    20% {
        width: 25%;
    }

    40% {
        width: 50%;
    }

    75% {
        width: 75%;
    }
}